import { AuthAPI } from "../api/api";
import { stopSubmit } from "redux-form";

const SET_USER_DATA = "app/auth/SET_USER_DATA";
const SET_AVATAR = "app/auth/SET_AVATAR";

let initialState = {
  userId: null,
  email: null,
  login: null,
  isAuth: false,
  avatar: null,
};

let authReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_USER_DATA: {
      return {
        ...state,
        ...action.data,
      };
    }
    case SET_AVATAR: {
      return {
        ...state,
        avatar: action.avatar,
      };
    }
    default:
      return state;
  }
};

export const setAuthUserData = (userId, login, email, isAuth) => {
  return { type: SET_USER_DATA, data: { userId, login, email, isAuth } };
};

export const setAvatar = (avatar) => {
  return { type: SET_AVATAR, avatar };
};

const getProfile = (id) => async (dispatch) => {
  let data = await AuthAPI.getProfile(id);
  dispatch(setAvatar(data.photos.small));
};

export const getAuthMe = () => async (dispatch) => {
  let data = await AuthAPI.getAuthMe();
  if (data.resultCode === 0) {
    let { id, login, email } = data.data;
    dispatch(setAuthUserData(id, login, email, true));
    getProfile(id);
  }
};

export const login = (email, password, rememberMe) => async (dispatch) => {
  let data = await AuthAPI.login(email, password, rememberMe);
  if (data.resultCode === 0) {
    debugger;
    dispatch(getAuthMe());
  } else {
    let message = data.messages.length > 0 ? data.messages[0] : "Some error";
    dispatch(stopSubmit("login", { _error: message }));
  }
};

export const logout = () => async (dispatch) => {
  let data = await AuthAPI.logout();
  if (data.resultCode === 0) {
    debugger;
    dispatch(setAuthUserData(null, null, null, false));
  }
};

export default authReducer;
